.share {
  height: 100%;
  display: -webkit-box;
  /* android 2.1-3.0, ios 3.2-4.3 */
  /* Chrome 21+ */
  display: -ms-flexbox;
  /* WP IE 10 */
  display: flex;
  /* android 4.4 */
  /* 水平布局下的子元素 垂直居中 */
  -webkit-box-align: center;
  /* android 2.1-3.0, ios 3.2-4.3 */
  /* Chrome 21+ */
  -ms-flex-align: center;
  /* WP IE 10 */
  align-items: center;
  /* android 4.4 */
  /* 水平布局下的子元素 水平居中 */
  -webkit-box-pack: center;
  /* android 2.1-3.0, ios 3.2-4.3 */
  /* Chrome 21+ */
  -ms-flex-pack: center;
  /* WP IE 10 */
  justify-content: center;
  /* android 4.4 */
}
.share-advert {
    width: 6.1rem;
    padding: 0.15rem;
    background: #f3f3f5;
    display: -webkit-box;
    /* android 2.1-3.0, ios 3.2-4.3 */
    /* Chrome 21+ */
    display: -ms-flexbox;
    /* WP IE 10 */
    display: flex;
    /* android 4.4 */
    /* 水平布局下的子元素 2端对齐 */
    -webkit-box-pack: justify;
    /* android 2.1-3.0, ios 3.2-4.3 */
    /* Chrome 21+ */
    -ms-flex-pack: justify;
    /* WP IE 10 */
    justify-content: space-between;
    /* android 4.4 */
    /* 水平布局下的子元素 垂直居中 */
    -webkit-box-align: center;
    /* android 2.1-3.0, ios 3.2-4.3 */
    /* Chrome 21+ */
    -ms-flex-align: center;
    /* WP IE 10 */
    align-items: center;
    /* android 4.4 */
}
.share-advert img {
      width: 1rem;
      height: 1rem;
}
